.project-detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  color: white;
  margin-top: 20px;
  gap:35px;
  font-family: "Orbitron", sans-serif;
}
.project-description{
  text-align: justify;
}
.project-title{
  color:aquamarine;
  font-size:2rem;
  text-align: center;
}


.project-images{
  display:flex;
  flex-direction: row;
  
}
.project-images img {
  margin: 5px; 
  border-radius: 8px;
  width: auto; 
  height: 150px; 
  object-fit: cover; 
  transition: transform 0.3s ease;
}

.project-images img:hover {
  transform: scale(1.05);
  background-color: rgba(2, 2, 73, 0.123);
}
.project-description{
  width:80%;
}

.project-tags {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 50%;
  padding: 20px;
}
.tag {
  padding: 5px 10px;
  border-radius: 25px;
  background-color: transparent;
  border:1px solid white;
}

.link {
  display: inline-flex;
  align-items: center;
  padding: 5px 10px;
  margin: 0 5px;
  text-decoration: none;
}

.link img {
  &:hover{
    transform: scale(1.2);
  }
}

.back-button {
  align-self: flex-start;
  margin: 10px;
  padding: 5px 10px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

.back-button:hover {
  background-color: aquamarine;
  color: white;
}
.imgWebsite{
  width:50px;
}
.imgCode{
  width:40px;
}