.presentation {
    padding: 50px;
    &-info {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 50px;
        width:500px;
        
        &_sub {
            padding: 30px;
            font-size: 1.2rem;
            color:$var-color-green;
        }
    }


    &-tags {
        display: flex;
        flex-direction: column;
        padding-left: 10%;
        gap: 5px;
        align-items: center;
        flex-wrap: wrap;
        text-align: center;
        padding:110px 0px;
        & h2 {
            color:aquamarine;
        }
        &_contact {
            opacity:0.9;
            display:flex;
            flex-direction: row;
            align-items: center;
            gap:30px;
           & img{
            border-radius: 360px;
            width: 40px;
            object-fit: cover;
            &:hover{
                transform:scale(1.2)
            }
           } 
        }
    }
}
.presentation-info_sub_anim{
    animation:clignoter 1s ease-in-out infinite
}

@keyframes clignoter {
    0%
    {
        opacity: 0;
    }
    100%{
        opacity:1;
    }
}

@media (max-width: 992px) {
.presentation{
        &-tags {
            padding-left: 0px;
        }
        &-profil{
            width:300px;
        }
    }
        .presentation-info{
            flex-direction: column-reverse;
            &_sub{
                width:350px;
            }
        }
}

@media (max-width: 500px) {
    .presentation {
        &-profil {
            margin: 0px;
            width: 250px;
        }
        &-info {
            flex-direction: column-reverse;
            &_sub {
                width: 200px;
            }
        }
        
    }
}