.about{
    display:flex;
    align-items: center;
    flex-direction: column;
    width:800px;
    padding-top:100px;
    & img{
        width:150px;
        opacity:0.9;
        border-radius:360px;
        padding:25px 0px;

    }
    &-p{
        animation:getit 3s ease-in;
        &1{
            animation:getit 3.5s ease-in;
        }
        &2{
            animation:getit 4s ease-in;
        }
        &3{
            animation:getit 4.5s ease-in;
        }
    }
    
}
@keyframes getit {
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}




